* {
  box-sizing: border-box;
}
body {
  background: #f5f5f5;
}
.user-container {
  .user-inner-section {
    .welcome-header {
      margin-top: 90px;
      display: flex;
      flex-direction: column;
      align-items: center;
      .header-logo-image {
        width: 200px;
        margin-bottom: 20px;
      }
    }
    .accout-section {
      #login-form {
        display: flex;
        flex-direction: column;
        margin: 0 30px;
        #id {
          border: 0;
          outline: 0;
          border-bottom: 1px solid rgba(0, 0, 0, 0.2);
          margin-bottom: 10px;
          transition: border-color 0.3s ease-in-out;
          font-size: 15px;
          padding: 13px 0;
        }
        #id:focus {
          border: 0;
          border-bottom: 1px solid red;
        }
      }
      #password {
        border: 0;
        outline: 0;
        border-bottom: 1px solid rgba(0, 0, 0, 0.2);
        margin-bottom: 10px;
        transition: border-color 0.3s ease-in-out;
        font-size: 15px;
        padding: 13px 0;
        color: #333;
      }
      #password:focus {
        border: 0;
        border-bottom: 1px solid red;
      }
      .login-button {
        background-color: skyblue;
        cursor: pointer;
        padding: 20px 0px;
        border-radius: 5px;
        border: 0;
        .login-button-text {
          text-decoration: none;
          color: #000;
          font-size: 17px;
        }
      }
      .find-information {
        text-decoration: none;
        color: #333;
        font-size: 15px;
        text-align: center;
        margin-top: 30px;
      }
    }
  }
}
