* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", "Noto Sans KR", sans-serif;
}

.event-product-container {
  width: 100%;
  background: #f5f5f5;
  .event-container-inner {
    width: 1200px;
    margin: 0 auto;
    padding: 20px 5px;
    .event-text {
      padding: 10px 5px;
    }
    .product-list {
      display: flex;
      font-size: 22px;
      padding: 10px 5px;
      list-style: none;
      &:after {
        content: "";
        clear: both;
        display: block;
      }
      .product-list-inner {
        width: 33.33%;
        text-align: center;
        padding: 0 10px;
        .product-image-container {
          display: block;
          overflow: hidden;
          position: relative;
          text-decoration: none;
          transition: 0.5s;
          &:hover {
            transform: scale(1.1);
          }
          .product-image {
            width: 100%;
          }
          .product-hashtag {
            position: absolute;
            bottom: 50px;
            left: 20px;
            .product-name {
              display: inline-block;
              background: rgba(0, 0, 0, 0.3);
              color: #fff;
              margin: 5px 2px;
              padding: 3px;
              font-size: 15px;
            }
          }
        }
        .product-text-wrap {
          background: #fff;
          padding: 15px;
          .product-event {
            display: block;
            font-size: 13px;
            text-align: center;
            font-style: normal;
            margin: auto;
            width: 70px;
            height: 25px;
            line-height: 25px;
            border-radius: 20px;
            border: 1px solid #222;
          }
          .event-text {
            font-size: 16px;
            text-align: center;
            padding: 10px 0 5px 10px;
            display: block;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }
          .event-date {
            display: block;
            font-size: 13px;
            text-align: center;
            padding-top: 5px;
          }
        }
      }
    }
  }
}
