* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", "Noto Sans KR", sans-serif;
}

.coupon-container {
  background: #f5f5f5;
  .coupon-container-header {
    padding: 30px 0;
    .coupon-header {
      width: 1200px;
      margin: 0 auto;
      .coupon-title {
        padding: 10px 5px;
      }
      .coupon-subtitle {
        font-size: 15px;
        color: #333;
      }
    }
  }

  .coupon-list-container {
    display: block;
    position: relative;
    width: 100%;
    height: auto;
    padding: 5px 0 30px;
    text-align: center;
    .coupon-list-wrap {
      width: 1200px;
      margin: 0 auto;
      list-style: none;
      .coupon-list {
        display: flex;
        flex-direction: column;
        width: 33.3%;
        display: inline-flex;
        .coupon-box {
          text-align: center;
          background: url(./coupon.png) no-repeat;
          background-color: #fff;
          margin: 0 0 15px 15px;

          .coupon-text {
            display: table-cell;
            width: 375px;
            height: 270px;
            vertical-align: middle;
            .coupon-title {
              display: block;
              font-size: 18px;
              font-weight: bold;
              text-align: center;
            }
            .coupon-sale {
              display: block;
              padding: 5px 0;
              font-size: 50px;
              font-weight: bold;
            }
            .coupon-info {
              margin-top: 10px;
              font-size: 15px;
              color: #888;
              text-align: center;
            }
          }
        }
      }
    }
  }
  .button-box {
    display: inline-flex;
    justify-content: space-around;
    background: #fff;
    margin-bottom: 20px;
    .coupon-btn {
      margin-top: 10px;
      border: 3px solid #666;
      border-radius: 5px;
      padding: 10px;
      width: 200px;
      background-color: #fff;
      .coupon-dw {
        margin-right: 5px;
        font-size: 20px;
        font-weight: bold;
        color: #000;
        text-align: center;
      }
    }
  }
}
