.header-container {
  background: #fff;

  .gnb-container {
    position: relative;
    margin: 10px 0 4px;
    box-shadow: 0 2px 7px 0 rgba(0, 0, 0, 0.102);

    .gnb-inner {
      position: relative;
      width: 1200px;
      height: 60px;
      margin: 0 auto;
      .gnb-menu-section {
        display: flex;
        justify-content: center;
        list-style: none;
      }
      .gnb-menu-list {
        padding-right: 25px;
      }
      .gnb-list-link {
        font-size: 15px;
        line-height: 60px;
        text-decoration: none;
        color: #333;
      }
    }
  }
}
