* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", "Noto Sans KR", sans-serif;
}

.best-container {
  width: 100%;
  background: #f5f5f5;
  .best-section {
    width: 1200px;
    margin: 0 auto;
    padding: 20px 5px;
    .best-title {
      padding: 10px 0;
    }
    .best-product-container {
      display: flex;
      font-size: 22px;
      list-style: none;
      padding: 0 10px;
      .best-product-list {
        background: #fff;
        width: 33.3%;
        text-align: center;
        padding: 0 10px;
        margin: 5px;
        .best-product-image {
          width: 300px;
          text-align: center;
        }
        .best-product-title {
          display: block;
          padding: 20px 0 20px 20px;
        }
        .best-product-count {
          display: inline-block;
          font-size: 20px;
          padding-left: 20px;
          color: #f43142;
          margin-right: 5px;
        }
        .best-product-cost {
          display: inline-block;
          font-size: 24px;
          font-weight: bold;
        }
        .best-product-won {
          font-size: 16px;
          margin-right: 5px;
        }
        .best-product-price {
          text-decoration: line-through;
          font-size: 13px;
          font-style: normal;
          color: #888;
        }
        .best-product-deliver {
          display: block;
          padding: 10px 0 20px 20px;
          font-size: 13px;
          color: #666666;
        }
      }
    }
  }
}
