* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.main-container {
  background: #fff;
  .header {
    background: #fff;
    width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .header-inner {
      .menu {
      }
      .header-logo-image {
        width: 160px;
      }
    }
    .form-wrap {
      position: relative;
      .form-search-section {
        width: 450px;
        height: 45px;
        border-radius: 25px;
        border: 1px solid #333;
        outline: none;
        a {
          border: none;
        }
      }
      .button {
        position: absolute;
        top: 5px;
        right: 10px;
        background-size: 20px;
        cursor: pointer;
        border: 0;
      }
    }
    .user-nav {
      .user {
        margin-right: 10px;
      }
    }
  }
  .main-banner-image {
    .banner-container {
      .banner-inner {
        .product-image {
          width: 100%;
        }
      }
    }
  }

  .barnd-gnb-icon-container {
    width: 1200px;
    margin: 0 auto;
    padding: 20px 0;
    .brand-gnb-icon {
      .brand-icon-image {
        margin-top: 10px;
        width: 88px;
      }
    }
  }
  .hot-product-container {
    width: 1200px;
    margin: 0 auto;
    padding: 20px 0;
    .hot-product-inner {
      .hot-product-swiper {
        padding: 0 5px;
        margin: 15px 0;
        .hot-product-slide {
          text-align: center;
          box-shadow: 0 2px 7px 0 rgba(0, 0, 0, 0.102);
          padding: 20px 0;
          border: 1px solid rgba(0, 0, 0, 0.102);
          .hot-product-name {
            font-size: 15px;
            font-weight: bold;
            padding: 0 10px;
            display: block;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }
          .hot-product-title {
            font-size: 13px;
            color: #666;
            padding: 0 10px;
            display: block;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }
          .hot-product-discount {
            font-style: normal;
            font-weight: bold;
            padding-left: 20px;
            font-size: 20px;
            color: #ee5555;
          }
          .hot-product-cost {
            font-size: 20px;
            padding-left: 20px;
          }
          .hot-product-won {
            font-size: 15px;
          }
        }
      }
    }
  }
  .footer-container {
    width: 100%;
    .footer-section {
      .footer-inner {
        background: #f5f5f5;
        .footer-info {
          width: 1200px;
          margin: auto;
          text-align: center;
          .footer-list-container {
            list-style: none;
            display: flex;
            .footer-list {
              flex-direction: column;
              padding: 15px 5px;
              &:nth-child(7) {
                font-weight: bold;
                color: #333;
              }
            }
          }
        }
      }
    }

    .company-container {
      width: 1200px;
      margin: 0 auto;
      display: flex;
      justify-content: center;
      align-items: center;
      padding-bottom: 30px;
      .company-inner {
        display: flex;
        align-items: center;
        padding: 20px 0;
        .footer-logo-image {
          width: 160px;
          height: 71px;
        }
        .company-addr-section {
          padding: 0 50px;
          .company-title {
            display: block;
            font-size: 13px;
            color: #666666;
          }
          .company-addr,
          .num1,
          .num2 {
            font-size: 13px;
            color: #666666;
          }
          .company-notice,
          .company-legal {
            font-size: 13px;
            color: #666666;
            padding: 5px 0;
            display: block;
          }
        }
      }

      .customer-center-container {
        .customer-center-inner {
          .customer-center {
            font-size: 12px;
            color: #666666;
            display: block;
          }
          .customer-center-ask {
            display: inline-block;
            padding: 8px 8px 8px 0;
            font-size: 13px;
            color: #666666;
          }
          .customer-center-number {
            font-size: 20px;
            color: #666666;
          }
          .customer-center-time {
            font-size: 12px;
            font-style: normal;
            display: block;
            color: #666666;
          }
        }
      }
    }
  }
}
