* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.shopping-container {
  width: 100%;
  .product-list {
    width: 1200px;
    margin: 0 auto;
    padding: 30px 0;
    .product-inner {
      .product-image-container {
        display: flex;
        list-style: none;
        .product-image-list {
          padding: 0 20px;
          width: 25%;
          .proudct-image {
            width: 260px;
          }
          .product-title {
            font-size: 13px;
            padding: 5px 0;
            display: block;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }
          .product-discount {
            font-style: normal;
            font-size: 13px;
            font-weight: bold;
            padding-right: 5px;
            color: #ee5555;
          }
          .product-price {
            font-size: 13px;
            font-style: normal;
            text-decoration: line-through;
            color: #666666;
          }
          .product-percent {
            display: inline-block;
            color: #ee5555;
          }
          .product-cost {
            font-size: 20px;
            padding-left: 5px;
            line-height: 18px;
            color: #000;
          }
          .product-won {
            font-size: 13px;
            font-weight: normal;
            color: #000;
          }
        }
      }
    }
  }
}
